<template>
  <DetailShell barTitle="toast">
    <div class="toast-demo">
      <c-button @click="defaultHandle"></c-button><br/>
      <c-button @click="topHandle"></c-button><br/>
      <c-button @click="bottomHandle"></c-button><br/>
      <c-button @click="durationHandle">1</c-button><br/>
      <c-button @click="errorHandle"></c-button><br/>
      <c-button @click="customHandle"></c-button><br/>
    </div>
  </DetailShell>
</template>
<script>
import DetailShell from '@/components/modules/app/DetailShell.vue'
import CButton from '@/components/button/index.vue'
export default {
  name: 'bannersDemo',
  components: {
    DetailShell,
    CButton
  },
  data () {
    return {}
  },
  methods: {
    // 
    defaultHandle (event) {
      let text = event.currentTarget.lastChild.nodeValue
      this.$toast(text)
    },
    // 
    topHandle (event) {
      let text = event.currentTarget.lastChild.nodeValue
      this.$toast({position: 'top', message: text})
    },
    // 
    bottomHandle (event) {
      let text = event.currentTarget.lastChild.nodeValue
      this.$toast({position: 'bottom', message: text})
    },
    // 1
    durationHandle (event) {
      let text = event.currentTarget.lastChild.nodeValue
      this.$toast({duration: 1000, message: text})
    },
    // 
    errorHandle (event) {
      let text = event.currentTarget.lastChild.nodeValue
      this.$toast({type: 'error', message: text})
    },
    // 
    customHandle (event) {
      let text = event.currentTarget.lastChild.nodeValue
      this.$toast({className: 'custom-toast', message: text})
    }
  }
}
</script>
<style lang="scss" scoped>
@import '@/assets/style/variables.scss';

.toast-demo{
  padding: 40px;
  line-height: 120px;
}
</style>
<style lang="scss">
@import '@/assets/style/variables.scss';

.custom-toast{
  background-color: $base-color !important;
  color: #FFF !important;
}
</style>



