<template>
  <button class="btn"
          :class='[btnClass, computedType, computedSize]'
          :disabled="disabled" @click="doClick">
    <canvas class="zk-ripple" @click="ripple"></canvas>
    <slot>{{ btnText }}</slot>
  </button>
</template>
<script>
import { getStyle, getStyleNumber } from './utils'
export default {
  name: 'Button',
  props: {
    // 
    btnText: {
      type: String,
      default: ''
    },
    // primarydisabled
    btnType: {
      type: String,
      default: ''
    },
    // xssmlxlfull
    btnSize: {
      type: String,
      default: 'full'
    },
    // , true
    disabled: {
      type: Boolean,
      default: false
    },
    // 
    speed: {
      type: Number,
      default: 20
    },
    // 
    opacity: {
      type: Number,
      default: 0.4
    },
    // 
    btnClass: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      color: '',
      radius: 0,
      oCanvas: null,
      context: null,
      initialized: false,
      speedOpacity: 0,
      timer: null,
      origin: {}
    }
  },
  computed: {
    // 
    computedType: function () {
      if (this.disabled) {
        return 'btn-disabled'
      }
      return this.btnType ? `btn-${this.btnType}` : ''
    },
    // 
    computedSize: function () {
      return this.btnSize ? `btn-${this.btnSize}` : ''
    }
  },
  methods: {
    init (el) {
      const oBtn = el.parentElement
      this.color = getStyle(el.parentElement, 'color')
      const w = getStyleNumber(oBtn, 'width')
      // 
      this.speedOpacity = (this.speed / w) * this.opacity
      // canvas 
      el.width = w
      el.height = getStyleNumber(oBtn, 'height')
      this.context = el.getContext('2d')
    },
    doClick (event) {
      this.$emit('click', event)
    },
    ripple (event) {
      // 
      if (this.timer) {
        window.cancelAnimationFrame(this.timer)
      }
      if (this.disabled) return // 
      this.el = event.target
      // 
      if (!this.initialized) {
        this.initialized = true
        this.init(this.el)
      }
      this.radius = 0
      // 
      this.origin.x = event.offsetX
      this.origin.y = event.offsetY
      this.context.clearRect(0, 0, this.el.width, this.el.height)
      this.el.style.opacity = this.opacity
      this.draw()
    },
    draw () {
      this.context.beginPath()
      // 
      this.context.arc(this.origin.x, this.origin.y, this.radius, 0, 2 * Math.PI, false)
      this.context.fillStyle = this.color
      this.context.fill()
      // 
      this.radius += this.speed
      this.el.style.opacity -= this.speedOpacity
      // 
      if (this.radius < this.el.width || this.el.style.opacity > 0) {
        this.timer = window.requestAnimationFrame(this.draw)
      } else {
        // 
        this.context.clearRect(0, 0, this.el.width, this.el.height)
        this.el.style.opacity = 0
      }
    }
  },
  destroyed () {
    // 
    if (this.timer) {
      window.cancelAnimationFrame(this.timer)
      this.timer = null
    }
  }
}
</script>
<style lang="stylus" scoped>
.btn {
  display: inline-block;
  font-weight: 400;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  user-select: none;
  border: 1px solid transparent;
  font-size: 32px;
  line-height: 1.5;
  border-radius: 5px;
  transition: background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  overflow: hidden;
  position: relative;
  outline: none;
  padding: 10px 20px;
}
.zk-ripple {
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
/*.btn:focus, .btn:hover {
  background-color: #0069d9;
  text-decoration: none;
}*/
.btn-primary {
  color: #fff;
  background-color: #0096E0;
  border-color: #0096E0;
}
.btn-primary:hover {
  color: #fff;
  background-color: #0096E0;
  border-color: #0096E0;
}
.btn-primary.disabled, .btn-primary:disabled {
  background-color: #007bff;
  border-color: #007bff;
}
.btn-primary:active {
  box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12), 0 2px 4px -1px rgba(0, 0, 0, 0.2);
}
.btn-disabled {
  color: #fff;
  background-color: #E5E5E5;
  border-color: #E5E5E5;
}
.btn-xs{
  width: 120px;
  height: 86px;
}
.btn-s{
  width: 200px;
  height: 86px;
}
.btn-m{
  width: 300px;
  height: 86px;
}
.btn-l{
  width: 420px;
  height: 86px;
}
.btn-xl{
  width: 600px;
  height: 86px;
}
.btn-full{
  width: 100%;
  height: 86px;
}
</style>



